import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  contactRequests!: any[];
  loading!: boolean;

  constructor(private afs: AngularFirestore) {
    this.contactRequests = [];
    this.loading = false;
  }

  ngOnInit(): void {
    this.loadData();
  }

  private loadData(): void {
    this.loading = true;
    this.afs.collection("ContactForm").valueChanges().subscribe((el: any[]) => {
      this.contactRequests = el;
      this.loading = false;
    })
  }
  columnDefs = [
    { field: 'name', sortable: true, filter: true },
    { field: 'emailAddress', sortable: true, filter: true },
    { field: 'subject', sortable: true, filter: true },
    { field: 'message', sortable: true, filter: true }

  ];

}
