import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApplyNow } from 'src/models/apply-now';
import { Course } from 'src/models/course';
import { ApplyNowFirebaseService } from '../apply-now-firebase.service';

@Component({
  selector: 'app-apply-now',
  templateUrl: './apply-now.component.html',
  styleUrls: ['./apply-now.component.scss']
})
export class ApplyNowComponent implements OnInit {

  courses!: Array<Course>;

  applyNowForm = new FormGroup({
    name: new FormControl(null, [Validators.required]),
    fatherName: new FormControl(null, [Validators.required]),
    cnic: new FormControl(null, [Validators.required]),
    phone: new FormControl(null, [Validators.required]),
    emailAddress: new FormControl(null, [Validators.required, Validators.email]),
    referralId: new FormControl(null),
    address: new FormControl(null, [Validators.required]),
    education: new FormControl(null, [Validators.required]),
    courses: new FormControl(null, [Validators.required])
  });

  constructor(private applyNowService: ApplyNowFirebaseService,
    private afs: AngularFirestore,
    private router: Router
  ) {

    this.courses = [];
  }

  ngOnInit(): void {
    this.loadData();
  }

  private loadData(): void {
    this.afs.collection("Courses").valueChanges().subscribe(res => {
      this.courses = res as any[];
    });
  }

  create(): void {
    this.applyNowService.add(this.applyNowForm.value as ApplyNow).then((response) => {
      this.applyNowForm.reset();
      this.router.navigateByUrl(`/user/challan-form/${response.id}`);
    }, (error) => {
      alert("Operation Failed. Please Try again");
    })
  }

}
