import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AmbassidorSignupFirebaseService } from '../ambassidor-signup-firebase.service';

@Component({
  selector: 'app-ambassador-signup',
  templateUrl: './ambassador-signup.component.html',
  styleUrls: ['./ambassador-signup.component.scss']
})
export class AmbassadorSignupComponent implements OnInit {

  form = new FormGroup({
    name: new FormControl(null, [Validators.required]),
    cnic: new FormControl(null, [Validators.required]),
    phone: new FormControl(null, [Validators.required]),
    emailAddress: new FormControl(null, [Validators.required]),
    education: new FormControl(null, [Validators.required]),
    experience: new FormControl(null, [Validators.required]),
    institute: new FormControl(null, [Validators.required]),
    address: new FormControl(null, [Validators.required])
  });

  referralCode: string = "";
  constructor(private ambassadorService: AmbassidorSignupFirebaseService) { }

  ngOnInit(): void {
  }

  create(): void {
    this.ambassadorService.create(this.form.value).then((response) => {
      this.form.reset();
      response.get().then((r: any) => {
        this.referralCode = `Your Refferal Code is "${r.id}", Please copy and save`;
      });
    }, error => {
      alert("Operation Failed. Please Try Again")
    })
  }
}
