import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-ambassidor-requests',
  templateUrl: './ambassidor-requests.component.html',
  styleUrls: ['./ambassidor-requests.component.scss']
})
export class AmbassidorRequestsComponent implements OnInit {

  columnDefs = [
    { field: 'name', filter: true, sortable: true },
    { field: 'phone', filter: true },
    { field: 'institute', sortable: true, filter: true },
    { field: 'experience', filter: true, sortable: true },
    { field: 'emailAddress', sortable: true, filter: true },
    { field: 'education', filter: true },
    { field: 'cnic', filter: true },
    { field: 'address', filter: true },
  ]

  rowsData!: any[];
  loading: boolean;

  constructor(private afs: AngularFirestore) {
    this.rowsData = [];
    this.loading = false;
  }

  ngOnInit(): void {
    this.loadData();
  }

  private loadData(): void {
    this.loading = true;
    this.afs.collection("AmbassidorsRequests").valueChanges().subscribe((data: any[]) => {
      this.rowsData = data
      this.loading = false;
    })
  }

}
