import { ContactComponent } from './contact/contact.component';
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AdminComponent } from './admin/admin.component';
import { CoursesComponent } from './courses/courses.component';
import { AngularFireAuthGuard, redirectLoggedInTo, redirectUnauthorizedTo } from "@angular/fire/auth-guard";
import { LoginComponent } from '../login/login.component';
import { ApplicationsComponent } from './applications/applications.component';
import { AmbassidorRequestsComponent } from './ambassidor-requests/ambassidor-requests.component';

const redirectUnauthorizedToLogin = () => redirectUnauthorizedTo(['/Admin/login']);
const redirectLoggedInToCourses = () => redirectLoggedInTo(['/courses']);
const routes: Routes = [
  {
    path: "",
    component: AdminComponent,
    children: [
      {
        path: "courses",
        component: CoursesComponent,
        canActivate: [AngularFireAuthGuard],
        data: {
          authGuardPipe: redirectUnauthorizedToLogin
        }
      },
      {
        path: "applications",
        component: ApplicationsComponent,
        canActivate: [AngularFireAuthGuard],
        data: {
          authGuardPipe: redirectUnauthorizedToLogin
        }
      },
      {
        path: "contact-us",
        component: ContactComponent,
        canActivate: [AngularFireAuthGuard],
        data: {
          authGuardPipe: redirectUnauthorizedToLogin
        }
      },
      {
        path: "ambassador-requests",
        component: AmbassidorRequestsComponent,
        canActivate: [AngularFireAuthGuard],
        data: {
          authGuardPipe: redirectUnauthorizedToLogin
        }
      },
      {
        path: "login",
        component: LoginComponent,
        canActivate: [AngularFireAuthGuard],
        data: {
          authGuardPipe: redirectLoggedInToCourses
        }
      },
      {
        path: "",
        pathMatch: 'full',
        redirectTo: 'courses',
      }
    ]
  },

]


@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class AdminRoutingModule { }
