import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-challan-form',
  templateUrl: './challan-form.component.html',
  styleUrls: ['./challan-form.component.scss']
})
export class ChallanFormComponent implements OnInit {

  applicationId!: string;
  application!: any;

  constructor(
    private afs: AngularFirestore,
    private router: ActivatedRoute) {

    this.applicationId = "";
    this.application = null;
  }

  ngOnInit(): void {
    this.loadData();
  }

  private loadData(): void {
    this.router.params.subscribe(params => {
      this.applicationId = params["id"];
      this.afs.collection("Applications").doc(this.applicationId).valueChanges().subscribe(resp => {
        this.application = resp;
        setTimeout(() => {
          window.print();
        }, 1000)
      })
    })
  }

  get TotalAmount(): number {
    let amounts = [];
    amounts = this.application?.courses?.map((el: any) => +el.price);
    return amounts.reduce((a: any, b: any) => a + b);
  }

}
